<template>
  <div class="container">
    <div class="card card-body">
      <h3 class="card-title">Latest Posts</h3>
      <div class="card-text">
        <ul class="list-group">
          <li
            class="list-group-item"
            v-for="(post, index) in posts"
            v-bind:item="post"
            v-bind:index="index"
            v-bind:key="post._id"
          >
            {{post.text}}
            <p
              class="float-left"
            >{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}</p>
            <button class="btn btn-danger btn-sm float-right" v-on:click="deletePost(post._id)">X</button>
          </li>
        </ul>
      </div>
    </div>
    <div class="card card-body mt-3">
      <div class="form-group">
        <label for="create-post">Say something..</label>
        <input
          class="form-control"
          type="text"
          id="create-post"
          v-model="text"
          placeholder="Create a post"
        >
      </div>
      <button class="btn btn-secondary btn-sm" v-on:click="createPost">Post</button>
    </div>
    <p class="error" v-if="error">{{ error }}</p>
  </div>
</template>


<script>
import PostService from "../PostService";

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: ""
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err;
    }
  },
  methods: {
    async createPost() {
      await PostService.createPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
};
</script>

